:root {
  --transition: 250ms cubic-bezier(.4, 0, .2, 1);
}

body {
  font-family: Roboto, sans-serif;
}

p, h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  margin-bottom: 0;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.button-container {
  justify-content: center;
  align-items: center;
  display: flex;
}

.search-form {
  justify-content: center;
  gap: 18px;
  margin-top: 20px;
  display: flex;
}

.input {
  width: 264px;
  letter-spacing: .04em;
  filter: drop-shadow(0 4px 4px rgba(0, 0, 0, .15));
  color: #434455;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #434455;
  border-radius: 4px;
  padding-left: 16px;
  font-size: 16px;
  line-height: 1.5;
}

.input::-ms-input-placeholder {
  color: #434455;
}

.input::placeholder {
  color: #434455;
}

.search-btn {
  letter-spacing: .04em;
  color: #4d5ae5;
  cursor: pointer;
  transition: color var(--transition), background-color var(--transition), border-color var(--transition), box-shadow var(--transition);
  background-color: #f4f4fd;
  border: 1px solid #e7e9fc;
  border-radius: 4px;
  padding: 8px 24px;
  font-family: inherit;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.5;
}

.search-btn:hover, .search-btn:focus {
  color: #fff;
  background-color: #404bbf;
  border: 1px solid rgba(0, 0, 0, 0);
  box-shadow: 0 3px 1px rgba(0, 0, 0, .1), 0 2px 1px rgba(0, 0, 0, .08), 0 2px 2px rgba(0, 0, 0, .12);
}

.load-more-hidden {
  display: none;
}

.load-more {
  min-width: 169px;
  height: 56px;
  letter-spacing: .04em;
  color: #fff;
  cursor: pointer;
  text-transform: uppercase;
  transition: background-color var(--transition), transform var(--transition);
  background-color: #4d5ae5;
  border: none;
  border-radius: 4px;
  margin: 0 auto 20px;
  padding: 16px 32px;
  font-family: inherit;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.5;
  display: block;
}

.load-more:hover {
  background-color: #404bbf;
  transform: scale(1.05);
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:disabled {
  cursor: not-allowed;
  background-color: #c1c1c1;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  display: flex;
}

.photo-card {
  width: 300px;
  height: 400px;
  transition: transform var(--transition), box-shadow var(--transition);
  background-color: #fff;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 4px 8px rgba(0, 0, 0, .2);
}

.photo-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, .3);
}

.photo-card img {
  width: 100%;
  height: 60%;
  object-fit: cover;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.info {
  padding: 15px;
}

.info-item {
  color: #2e2f42;
  letter-spacing: .02em;
  margin-bottom: 5px;
  font-size: 18px;
  font-weight: 500;
  line-height: 1.2;
}

.info-item span {
  color: #434455;
  letter-spacing: .02em;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.5;
}

